.loader-back {
  background: #00000043;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.loader {
  color: #fff;
  width: 6px;
  height: 40px;
  animation: .75s ease-out infinite paddles;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader:before {
  content: "";
  background-color: #de3500;
  border-radius: 50%;
  width: 12px;
  height: 12px;
  margin: 0 auto;
  animation: .6s ease-out infinite ballbounce;
  position: absolute;
  top: 15px;
  left: 0;
  right: 0;
}

@keyframes paddles {
  0% {
    box-shadow: -25px -10px, 25px 10px;
  }

  50% {
    box-shadow: -25px 8px, 25px -10px;
  }

  100% {
    box-shadow: -25px -10px, 25px 10px;
  }
}

@keyframes ballbounce {
  0% {
    transform: translateX(-20px)scale(1, 1.2);
  }

  25% {
    transform: scale(1.2, 1);
  }

  50% {
    transform: translateX(15px)scale(1, 1.2);
  }

  75% {
    transform: scale(1.2, 1);
  }

  100% {
    transform: translateX(-20px);
  }
}

.tui-page-btn {
  border: 0;
  border-radius: 8px;
  width: 50px;
  display: block;
  box-shadow: 0 0 5px #0006;
}

